'use client'

import { useState } from 'react'
import { signIn } from 'next-auth/react'

import { Button } from '@/components/ui/button'
import { cn } from '@/lib/utils'
import { errorToast, loadingToast } from '@/lib/utils'

interface GoogleAuthButtonProps {
  className?: string
  onClick?: () => void
}

export function GoogleAuthButton({ className, onClick }: GoogleAuthButtonProps) {
  const [loading, setLoading] = useState(false)

  const handleGoogleSignIn = async () => {
    const loadingId = 'google-signin'

    try {
      setLoading(true)
      loadingToast.start('Connecting to Google...', loadingId)

      // Call external onClick handler first (for localStorage storage)
      if (onClick) {
        onClick()
      }

      await signIn('google', {
        callbackUrl: '/dashboard/drive',
      })

      // Note: Success toast will be shown after redirect in the dashboard
      loadingToast.dismiss(loadingId)
    } catch (error) {
      loadingToast.error('Failed to sign in with Google', loadingId)
      errorToast.generic('Sign in failed', {
        description: 'Please try again or check your connection',
        duration: 5000,
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <Button onClick={handleGoogleSignIn} disabled={loading} variant="outline" className={cn('relative', className)}>
      <svg className="mr-2 h-4 w-4" viewBox="0 0 24 24">
        <path
          fill="currentColor"
          d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
        />
        <path
          fill="currentColor"
          d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
        />
        <path
          fill="currentColor"
          d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
        />
        <path
          fill="currentColor"
          d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
        />
      </svg>
      {loading ? 'Signing in...' : 'Sign in with Google'}
    </Button>
  )
}
